<template>
  <v-container fluid class="down-top-padding">
    <v-row>
      <h1 class="display-1 font-weight-regular">Manage Students</h1>
    </v-row>
    <v-row>
      <v-spacer></v-spacer>
      <v-btn color="primary" dark @click="showForm(null)">
        <v-icon left>
          mdi-pencil-plus-outline
        </v-icon>
        Add New Student
      </v-btn>
    </v-row>
    <StudentForm ref="student_form" />
    <v-row>
      <v-data-table
        :page="getCurrentPage"
        :pageCount="getNumberOfPages"
        :headers="getTableHeader"
        :items="getAllStudents"
        :footer-props="{
          'items-per-page-options': [10, 20, 30, 40, 50, 100]
        }"
        :options.sync="options"
        :server-items-length="getTotalStudents"
        :loading="getLoadingStatus"
        class="elevation-1 col-12"
      >
        <template v-slot:item.nic="{ item }">
          {{ item.data.attributes.nic }} /
          {{ item.data.attributes.passport }}
        </template>
        <template v-slot:item.action="{ item }">
          <v-btn
            class="ma-1"
            text
            icon
            color="secondary"
            @click="showForm(item.data.attributes.id)"
          >
            <v-icon>mdi-pencil-outline</v-icon>
          </v-btn>

          <v-btn
            text
            icon
            color="danger"
            @click="showDeleteDialog(item.data.attributes)"
          >
            <v-icon>mdi-trash-can-outline</v-icon>
          </v-btn>
        </template>
      </v-data-table>

      <v-dialog v-model="dialogDelete" max-width="500px">
        <v-card>
          <v-card-title>Are you sure ?</v-card-title>
          <v-card-text
            >Do you want to delete `{{ itemToDelete.first_name }}`?
          </v-card-text>
          <v-card-actions>
            <v-btn color="primary" @click="dialogDelete = false">
              <v-icon>mdi-close</v-icon>
              No
            </v-btn>
            <v-btn color="error" @click="deleteItem(itemToDelete.id)">
              <v-icon>mdi-trash-can-outline</v-icon>
              Yes
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </v-container>
</template>

<script>
import { mapState, mapGetters, mapActions } from "vuex";
import StudentForm from "@/views/student/StudentForm";

export default {
  name: "Student",
  components: {
    StudentForm
  },
  computed: {
    ...mapGetters("student", [
      "getStudent",
      "getAllStudents",
      "getStudentDeleted",
      "getLoadingStatus",
      "getCurrentPage",
      "getTotalStudents",
      "getNumberOfPages",
      "getTableHeader"
    ])
  },
  data: () => ({
    ...mapState("student", ["student"]),
    dialogDelete: false,
    itemToDelete: {},
    options: {}
  }),

  watch: {
    options: {
      handler() {
        let payload = this.options;
        this.searchStudents(payload);
      }
    },
    deep: true
  },

  methods: {
    ...mapActions("student", ["searchStudents", "deleteStudent"]),
    showDeleteDialog(item) {
      this.itemToDelete = item;
      this.dialogDelete = !this.dialogDelete;
    },
    async deleteItem(itemId) {
      await this.deleteStudent(itemId);
      if (this.getStudentDeleted == true) {
        this.dialogDelete = false;
      }
    },
    showForm(studentId) {
      this.$refs.student_form.showFormHandler(studentId);
    }
  }
};
</script>

<style scoped></style>
