<template>
  <div>
    <v-row class="mb-5 mt-2">
      <v-spacer></v-spacer>
      <v-dialog v-model="dialog" persistent max-width="600px">
        <v-card>
          <v-card-title>
            <span class="headline">Student Information</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    label="First name*"
                    required
                    v-model="first_name"
                  ></v-text-field>
                </v-col>

                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    label="Last name*"
                    v-model="last_name"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-menu
                    ref="menu"
                    v-model="menu"
                    :close-on-content-click="false"
                    :return-value.sync="date_of_birth"
                    transition="scale-transition"
                    offset-y
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="date_of_birth"
                        required
                        label="Date of birth*"
                        prepend-icon="mdi-calendar"
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-date-picker v-model="date_of_birth" no-title scrollable>
                      <v-spacer></v-spacer>
                      <v-btn text color="primary" @click="menu = false">
                        Cancel
                      </v-btn>
                      <v-btn
                        text
                        color="primary"
                        @click="$refs.menu.save(date_of_birth)"
                      >
                        OK
                      </v-btn>
                    </v-date-picker>
                  </v-menu>
                </v-col>

                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    label="NIC"
                    required
                    v-model="nic"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    label="Passport"
                    v-model="passport"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-container>
            <small>*indicates required field</small>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="warning" dark @click="dialog = false">
              <v-icon left>
                mdi-close
              </v-icon>
              Close
            </v-btn>
            <v-btn color="primary" dark @click="studentCreateHandler">
              <v-icon left>
                mdi-content-save
              </v-icon>
              Save
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  name: "StudentForm",
  data: () => ({
    menu: false,
    dialog: false
  }),
  computed: {
    ...mapGetters("student", ["getStudentCreated", "getStudent"]),
    first_name: {
      get() {
        return this.$store.state.student.student.first_name;
      },
      set(value) {
        this.$store.commit("student/updateFirstName", value);
      }
    },
    last_name: {
      get() {
        return this.$store.state.student.student.last_name;
      },
      set(value) {
        this.$store.commit("student/updateLastName", value);
      }
    },
    date_of_birth: {
      get() {
        return this.$store.state.student.student.date_of_birth;
      },
      set(value) {
        this.$store.commit("student/updateDateOfBirth", value);
      }
    },
    nic: {
      get() {
        return this.$store.state.student.student.nic;
      },
      set(value) {
        this.$store.commit("student/updateNic", value);
      }
    },
    passport: {
      get() {
        return this.$store.state.student.student.passport;
      },
      set(value) {
        this.$store.commit("student/updatePassport", value);
      }
    }
  },
  methods: {
    ...mapActions("student", ["addStudent", "fetchStudent"]),
    async studentCreateHandler() {
      await this.addStudent();
      if (this.getStudentCreated == true) {
        this.dialog = false;
      }
    },
    async showFormHandler(studentId) {
      this.clearForm();
      if (studentId != null) {
        await this.fetchStudent(studentId);
      }
      this.dialog = true;
    },
    clearForm() {
      this.first_name = null;
      this.last_name = null;
      this.date_of_birth = null;
      this.nic = null;
      this.passport = null;
    }
  }
};
</script>

<style scoped></style>
